"use client";

import { useCurrentAccount, useSignAndExecuteTransaction, useIotaClient } from "@iota/dapp-kit";
import { Transaction } from "@iota/iota-sdk/transactions";
import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import { toast } from "sonner";
import { PACKAGE_ID, MODULE_NAME, RANDOM_OBJECT_ID } from "@/utils/constants";

export function GameDashboard() {
  const account = useCurrentAccount();
  const client = useIotaClient();
  const { mutate: signAndExecuteTransaction } = useSignAndExecuteTransaction();

  const [loading, setLoading] = useState(false);
  const [investigator, setInvestigator] = useState<any>(null);
  
  // Ëá™ÂãïÊäìÂèñÁé©ÂÆ∂ÊìÅÊúâÁöÑË™øÊü•Âì°Áâ©‰ª∂
  const fetchInvestigator = async () => {
    if (!account) return;
    
    // 1. ÊäìÂèñË©≤Â∏≥ËôüÊìÅÊúâÁöÑÊâÄÊúâÁâ©‰ª∂
    const objects = await client.getOwnedObjects({
      owner: account.address,
      options: { showType: true, showContent: true }
    });

    // 2. ÈÅéÊøæÂá∫ÊàëÂÄëÁöÑ Investigator Áâ©‰ª∂
    // Type Ê†ºÂºè: PackageID::Module::Struct
    const targetType = `${PACKAGE_ID}::${MODULE_NAME}::Investigator`;
    const found = objects.data.find((obj) => obj.data?.type === targetType);

    if (found && found.data?.content?.dataType === "moveObject") {
      // @ts-ignore
      setInvestigator(found.data.content.fields);
      // @ts-ignore
      setInvestigator(prev => ({ ...prev, id: found.data.objectId }));
    }
  };

  useEffect(() => {
    fetchInvestigator();
  }, [account]);

  // 1. ÈëÑÈÄ†ËßíËâ≤ (Mint)
  const mint = () => {
    setLoading(true);
    const tx = new Transaction();
    
    // ÂèÉÊï∏ÔºöRandomÁâ©‰ª∂, ÂêçÂ≠ó (ËΩâÊàê bytes)
    tx.moveCall({
      target: `${PACKAGE_ID}::${MODULE_NAME}::mint`,
      arguments: [
        tx.object(RANDOM_OBJECT_ID),
        tx.pure.vector("u8", Array.from(new TextEncoder().encode("Detective John")))
      ],
    });

    executeTx(tx, "ÈëÑÈÄ†Ë™øÊü•Âì°");
  };

  // 2. ÁêÜÊô∫Ê™¢ÂÆö (Sanity Check)
  const sanityCheck = () => {
    if (!investigator) return;
    setLoading(true);
    const tx = new Transaction();

    // ÂèÉÊï∏ÔºöË™øÊü•Âì°Áâ©‰ª∂, RandomÁâ©‰ª∂
    tx.moveCall({
      target: `${PACKAGE_ID}::${MODULE_NAME}::sanity_check`,
      arguments: [
        tx.object(investigator.id),
        tx.object(RANDOM_OBJECT_ID)
      ],
    });

    executeTx(tx, "ÁêÜÊô∫Ê™¢ÂÆö");
  };

  // ÈÄöÁî®‰∫§ÊòìÂü∑Ë°åÂáΩÂºè
  const executeTx = (tx: Transaction, actionName: string) => {
    // ÈóúÈçµÔºöRandom Ê®°ÁµÑÈÄöÂ∏∏ÈúÄË¶ÅËºÉÈ´òÁöÑ Gas È†êÁÆó
    tx.setGasBudget(50000000); 

    signAndExecuteTransaction(
      { transaction: tx, options: { showEffects: true, showEvents: true } },
      {
        onSuccess: (result) => {
            console.log("‰∫§ÊòìÊàêÂäü:", result);
            toast.success(`${actionName} ÊàêÂäüÔºÅ`);
            
            // Â¶ÇÊûúÊúâ EventÔºåËß£Êûê‰∏¶È°ØÁ§∫ÁµêÊûú
            const event = result.events?.find(e => e.type.includes("CheckEvent"));
            if (event) {
                // @ts-ignore
                const msg = event.parsedJson?.msg;
                // @ts-ignore
                const isSuccess = event.parsedJson?.is_success;
                toast(isSuccess ? "üéâ Â§ßÊàêÂäüÔºÅ" : "üò± Ê™¢ÂÆöÂ§±ÊïóÔºÅ", {
                    description: msg,
                    action: { label: "ÈóúÈñâ", onClick: () => {} }
                });
            }

            setLoading(false);
            setTimeout(fetchInvestigator, 1000); // Âª∂ÈÅ≤Âà∑Êñ∞Êï∏Êìö
        },
        onError: (err) => {
            console.error(err);
            toast.error(`‰∫§ÊòìÂ§±Êïó: ${err.message}`);
            setLoading(false);
        }
      }
    );
  };

  // ÂúñÁâáÈ°ØÁ§∫ÈÇèËºØ
  const getImageUrl = () => {
    if (!investigator) return "/images/egg.png";
    if (investigator.is_mad) return "/images/madness.png";
    return "/images/investigator.png";
  };

  if (!account) {
    return <div className="text-center p-10 text-xl text-yellow-400">‚ö†Ô∏è Ë´ãÂÖàÈÄ£Êé•Âè≥‰∏äËßíÈå¢ÂåÖ</div>;
  }

  return (
    <div className="max-w-md mx-auto mt-10">
      <Card className="bg-slate-900 border-slate-700 text-slate-100 shadow-2xl">
        <CardHeader>
          <CardTitle className="flex justify-between items-center">
            <span>üïµÔ∏è ËàäÊó•Ë™øÊü•Âì°</span>
            {investigator && <span className="text-xs text-slate-500">Lv.1</span>}
          </CardTitle>
          <CardDescription className="text-slate-400">
             {investigator ? `ID: ${investigator.id.slice(0,6)}...` : "Â∞öÊú™ÊãõÂãü"}
          </CardDescription>
        </CardHeader>
        
        <CardContent className="flex flex-col items-center gap-6">
          {/* ËßíËâ≤ÂúñÁâá */}
          <div className={`relative w-64 h-64 rounded-xl overflow-hidden border-4 transition-all duration-500 ${investigator?.is_mad ? 'border-red-600 shadow-[0_0_30px_rgba(220,38,38,0.6)]' : 'border-slate-600'}`}>
             <img src={getImageUrl()} alt="Character" className="w-full h-full object-cover" />
             {investigator?.is_mad && (
                 <div className="absolute inset-0 bg-red-900/30 flex items-center justify-center">
                     <span className="text-4xl font-bold text-red-500 rotate-12 border-4 border-red-500 p-2">MADNESS</span>
                 </div>
             )}
          </div>

          {/* Êï∏ÂÄºÈù¢Êùø */}
          {investigator ? (
            <div className="w-full space-y-4">
               <div className="grid grid-cols-2 gap-4 text-center">
                   <div className="bg-slate-800 p-2 rounded">
                       <div className="text-xs text-slate-400">STR (ÂäõÈáè)</div>
                       <div className="text-xl font-bold text-orange-400">{investigator.str}</div>
                   </div>
                   <div className="bg-slate-800 p-2 rounded">
                       <div className="text-xs text-slate-400">DEX (ÊïèÊç∑)</div>
                       <div className="text-xl font-bold text-green-400">{investigator.dex}</div>
                   </div>
               </div>

               <div className="space-y-1">
                   <div className="flex justify-between text-sm">
                       <span className={investigator.is_mad ? "text-red-500 font-bold" : "text-blue-400"}>
                           SAN (ÁêÜÊô∫ÂÄº)
                       </span>
                       <span>{investigator.san} / {investigator.max_san}</span>
                   </div>
                   <Progress 
                       value={(investigator.san / investigator.max_san) * 100} 
                       className={`h-3 bg-slate-800 ${investigator.is_mad ? "[&>div]:bg-red-600" : "[&>div]:bg-blue-500"}`} 
                   />
               </div>

               <div className="space-y-1">
                   <div className="flex justify-between text-sm text-yellow-500">
                       <span>EXP (Á∂ìÈ©ó)</span>
                       <span>{investigator.exp}</span>
                   </div>
                   <Progress value={Math.min(investigator.exp, 100)} className="h-2 bg-slate-800 [&>div]:bg-yellow-500" />
               </div>

               <Button 
                   onClick={sanityCheck} 
                   disabled={loading || investigator.is_mad} 
                   className="w-full h-12 text-lg bg-indigo-600 hover:bg-indigo-500 disabled:opacity-50 disabled:cursor-not-allowed"
               >
                   {loading ? "üé≤ Êì≤È™∞‰∏≠..." : "üé≤ ÈÄ≤Ë°åÁêÜÊô∫Ê™¢ÂÆö (SAN Check)"}
               </Button>
            </div>
          ) : (
            <Button onClick={mint} disabled={loading} className="w-full h-12 text-lg bg-emerald-600 hover:bg-emerald-500">
                {loading ? "Âè¨Âñö‰∏≠..." : "üïØÔ∏è ÊãõÂãüË™øÊü•Âì° (Mint)"}
            </Button>
          )}
        </CardContent>
      </Card>
    </div>
  );
}